.main404{font-size:16px;text-align:center;overflow-y:auto;overflow-x:hidden;transition:all .6s cubic-bezier(.34,1.56,.64,1);position:absolute;background:rgba(255,255,255,.1);box-shadow:0 20px 50px rgb(0 0 0 / 15%);border-top:1px solid rgba(255,255,255,.5);border-left:1px solid rgba(255,255,255,.5);backdrop-filter:blur(5px);color:var(--text-color);top:-100%;border-radius:0;animation:slidetop .5s forwards;padding:125px 0}.error1-card{animation:.7s ease 0s 1 normal none running zoomin;padding:10px;user-select:none;position:relative;display:flex;flex-direction:column;min-width:0;word-wrap:break-word;box-shadow:0 1px 4px rgb(0 0 0 / 30%),0 0 40px rgb(0 0 0 / 10%) inset!important;transition:all .3s cubic-bezier(.25,.8,.25,1);border-radius:10px;border:none;color:var(--text-color);width:fit-content;background:rgba(255,255,255,.1);border-top:1px solid rgba(255,255,255,.5);border-left:1px solid rgba(255,255,255,.5);backdrop-filter:blur(5px)}.error1-h3{font-family:"Mr Dafoe";margin:0;font-size:5.5em;margin-top:-1.6em;color:white;text-shadow:0 0 .05em #fff,0 0 .2em #fe05e1,0 0 .3em #fe05e1;transform:rotate(-7deg)}.error1-p{font-size:calc(1.675rem+1.1vw)!important;-webkit-text-stroke-width:1px;text-stroke-width:1px;-webkit-text-stroke-color:#ffffff;-moz-text-stroke-color:#ffffff;text-stroke-color:#ffffff;color:transparent}